<template>
  <div class="main-wrapper">
    <section class="blog-list px-3 py-5 p-md-5">
      <div class="container">
        <h2 class="heading" style="text-align: center">根据 Tag 快速检索文章</h2>
        <tag-button
          v-for="tagname in $store.getters.tag_list"
          :key="tagname"
          :tagname="tagname"
        ></tag-button>
      </div>
    </section>
    <section v-if="!placeholder" class="blog-list px-3 py-5 p-md-5">
        <div class="container">
                    <div
          v-for="item in this.tag_list.slice(
            currentPage * 10,
            currentPage * 10 + 10
          )"
          v-bind:key="this.$store.state.post_list[item].title"
          class="item mb-5"
        >
          <div class="media">
            <img
              class="mr-3 img-fluid post-thumb d-none d-md-flex"
              :src="'/post/images/'+ this.$store.state.post_list[item].time_str.replaceAll('-', '')+'/1.png'"
              onerror="this.src='../assets/logo.png'"
            />
            <div class="media-body">
              <h3 class="title mb-1">
                <a :href="'/post/html/'+this.$store.state.post_list[item].url" target="_blank">{{ this.$store.state.post_list[item].title }}</a>
              </h3>
              <div class="meta mb-1">
                <span class="date">{{ this.$store.state.post_list[item].time_str }}</span>
                <span class="time">{{ this.$store.state.post_list[item].read_time }}</span>
                <span class="comment"
                  ><a href="#">{{ this.$store.state.post_list[item].comments_num }}</a></span
                >
              </div>
              <div class="intro intro2">{{ this.$store.state.post_list[item].desc }}</div>
              <a class="more-link" :href="this.$store.state.post_list[item].url">Read more &rarr;</a>
            </div>
            <!--//media-body-->
          </div>
          <!--//media-->
        </div>
        </div>
    </section>

    <section class="blog-list px-3 py-5 p-md-5">
        <div v-if="placeholder" style="height: 100px"></div>
    </section>
    <footer class="footer text-center py-2 theme-bg-dark">
      <!--/* This template is released under the Creative Commons Attribution 3.0 License. Please keep the attribution link below when using for your own project. Thank you for your support. :) If you'd like to use the template without the attribution, you can buy the commercial license via our website: themes.3rdwavemedia.com */-->
      <small class="copyright"
        >Designed with <i class="fas fa-heart" style="color: #fb866a"></i> by
        <a href="http://themes.3rdwavemedia.com" target="_blank"
          >Xiaoying Riley</a
        >
        for developers</small
      >
    </footer>
  </div>
</template>

<script>
import TagButton from "../components/TagButton.vue";
export default {
  name: "Tags",
  components: {
    TagButton,
  },
  data(){
      return {
          placeholder: true,
          currentPage: 0,
          tag_list: []
      }
  },
  beforeUpdate() {
      console.log(this.$store.state.tags_table)
    let tagname = ""
    tagname = this.$route.params.tagname
    if (tagname == ""){
        this.placeholder = true;
        this.tag_list = []
    }
    else{
        this.placeholder = false
      this.tag_list = this.$store.state.tags_table[tagname]
    }
    this.currentPage = 0
  }
};
</script>
